
.animationParent{
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.animationContent{
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  flex-direction: column;

}
.animationContainer .marquee{

display: flex;
justify-self: center;

}

   .animationText p{
    font-size: 180px;
    font-family:canela;
    text-align: left;
    
  }
  .animationText p span{
    font-size: 80px;
    letter-spacing: 0;
    margin: 4rem;
    margin-left: 0;
  }
   @media screen and (max-width:1350px ){
    .animationContainer{
      max-width: 1350px;

    }
    .animationText p{
      font-size: 100px;
    
    }
    .animationText p span{
      font-size: 40px;
      letter-spacing: 0;
    }
   }
   @media screen and (max-width:950px ) {
    .animationContainer {
      max-width: 950px;
     }
     .animationText p{
      font-size: 50px;
    }
    .animationText p span{
      font-size: 20px;
      letter-spacing: 0;
    }
  
  } 
  @media screen and (max-width:768px ){
    .animationContainer{
      max-width: 768px;
    }
    .animationText p{
      font-size: 30px;
    }
    .animationText p span{
      font-size: 10px;
      letter-spacing: 0;
    }
  }
  @media screen and (max-width:414px ){
  
    .animationText p{
      font-size: 15px;
    }
    .animationText p span{
      font-size: 5px;
      letter-spacing: 0;
    }
  }
  @media screen and (max-width:280px ){
    .animationText p{
      font-size: 10px;
    }
    .animationText p span{
      font-size: 5px;
      letter-spacing: 0;
    }
  }
  